<template>
    <div class="cart">
      <van-nav-bar title="购物车" fixed />
      <!-- 购物车开头 -->
      <div class="cart-title">
        <span class="all">共<i>{{ cartTotal || 0 }}</i>件商品</span>
        <span class="edit">
          <van-icon name="edit" @click="bian"/>
          编辑
        </span>
      </div>
  
      <!-- 购物车列表 -->
      <div class="cart-list">
        <div class="cart-item" v-for="(item) in cartList" :key="item.goods_id">
          <van-checkbox :value="item.ischecked" @click="qie(item.goods_id)"></van-checkbox>
          <div class="show">
            <img :src=item.goods.goods_image alt="">
          </div>
          <div class="info">
            <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
            <span class="bottom">
              <div class="price">¥ <span>{{  item.goods.goods_price_max}}</span></div>
                  <CountBox @input="value => count(value, item.goods_id, item.goods_sku_id)" :value="item.goods_num" :item="item"></CountBox>
            </span>
          </div>
        </div>
      </div>
  
      <div class="footer-fixed">
        <div  class="all-check" @click="quan1(!isquan)">
          <van-checkbox  icon-size="18" :value="all"></van-checkbox>
          全选
        </div>
  
        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span>¥ <i class="totalPrice">{{ selPrice }}</i></span>
          </div>
          <div v-if="!isedit" :class="{ disabled: selCount === 0 }" class="goPay" @click="$router.push('/myorder')">结算({{ selCount }})</div>
          <div v-else :class="{ disabled: selCount === 0 }" class="delete" @click="shan">删除</div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { chelist ,update} from '@/api/jiacar';
  // import user from '@/store/modules/user';
  import store from '@/store';
  import { mapActions, mapGetters, mapMutations, mapState } from 'vuex'
  import CountBox from '@/components/CountBox.vue';
  export default {
    data(){
      return {
        isquan:true,
        isedit:false
      }
    },
    components:{
      CountBox
    },
    wetch:{
      isedit(x){
        if(x){
          this.$store.commit('cart/quan',false)
        }
      }
    },
    name: 'CartPage',
    async created(){
      if(this.$store.getters.token){
        console.log('token exist');
        this.getcarlist()
      }
        // const res=await chelist()
        // console.log(res.data.data.list);
        // this.$store.commit('setlist',res.data.data.list)

        const res =await update({
          "goodsId": 10038,
          "goodsSkuId": "0",
          "goodsNum": 2
        })
        console.log(res.data);
    },
    computed:{
      ...mapState('cart',['cartList']),
      ...mapGetters('cart', ['cartTotal', 'selCount', 'selPrice','all'])
    },
    methods:{
      ...mapActions('cart',['getcarlist']),
      ...mapMutations('cart',['quan']),
      qie(x){
        this.$store.commit('cart/xuan',x)
      },
      quan1(x){
        this.$store.commit('cart/quan',x)
        this.isquan=!this.isquan
      },
      count(x,y,z){
        const obj={
          goodsId: y,
          goodsNum: x,
          goodsSkuId: z
        }
        console.log(x,y,z);
        this.$store.dispatch('cart/up',obj)
      },
      bian(x){
        this.isedit=!this.isedit
        this.$store.commit('cart/quan',!x)
      },
      shan(){
        if(this.selCount===0) return
        this.$store.dispatch('cart/shan')
      }
    }
  }
  </script>
  
  <style lang="less" scoped>
  // 主题 padding
  .cart {
    padding-top: 46px;
    padding-bottom: 100px;
    background-color: #f5f5f5;
    min-height: 100vh;
    .cart-title {
      height: 40px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 10px;
      font-size: 14px;
      .all {
        i {
          font-style: normal;
          margin: 0 2px;
          color: #fa2209;
          font-size: 16px;
        }
      }
      .edit {
        .van-icon {
          font-size: 18px;
        }
      }
    }
  
    .cart-item {
      margin: 0 10px 10px 10px;
      padding: 10px;
      display: flex;
      justify-content: space-between;
      background-color: #ffffff;
      border-radius: 5px;
  
      .show img {
        width: 100px;
        height: 100px;
      }
      .info {
        width: 210px;
        padding: 10px 5px;
        font-size: 14px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
  
        .bottom {
          display: flex;
          justify-content: space-between;
          .price {
            display: flex;
            align-items: flex-end;
            color: #fa2209;
            font-size: 12px;
            span {
              font-size: 16px;
            }
          }
          .count-box {
            display: flex;
            width: 110px;
            .add,
            .minus {
              width: 30px;
              height: 30px;
              outline: none;
              border: none;
            }
            .inp {
              width: 40px;
              height: 30px;
              outline: none;
              border: none;
              background-color: #efefef;
              text-align: center;
              margin: 0 5px;
            }
          }
        }
      }
    }
  }
  
  .footer-fixed {
    position: fixed;
    left: 0;
    bottom: 50px;
    height: 50px;
    width: 100%;
    border-bottom: 1px solid #ccc;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
  
    .all-check {
      display: flex;
      align-items: center;
      .van-checkbox {
        margin-right: 5px;
      }
    }
  
    .all-total {
      display: flex;
      line-height: 36px;
      .price {
        font-size: 14px;
        margin-right: 10px;
        .totalPrice {
          color: #fa2209;
          font-size: 18px;
          font-style: normal;
        }
      }
  
      .goPay, .delete {
        min-width: 100px;
        height: 36px;
        line-height: 36px;
        text-align: center;
        background-color: #fa2f21;
        color: #fff;
        border-radius: 18px;
        &.disabled {
          background-color: #ff9779;
        }
      }
    }
  
  }
  </style>